<template>
  <div class="game-board">
    <table>
      <thead>
        <tr>
          <th v-for="(category, index) in categories" :key="index">{{ category }}</th>
        </tr>
      </thead>
      <tbody>
        <!-- Loop through up to 5 questions for each category -->
        <tr v-for="rowIndex in 5" :key="rowIndex">
          <td v-for="(category, index) in categories" :key="index">
            <!-- Check if the category has enough questions for this row -->
            <v-btn
              v-if="displayedQuestions[category][rowIndex - 1]"
              :class="{ 'greyed-out': isOpened(category, rowIndex - 1) }"
              @click="$emit('ask-question', { category, index: rowIndex - 1 })"
            >
              {{ displayedQuestions[category][rowIndex - 1].points }} POINTS
            </v-btn>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    categories: Array,
    displayedQuestions: Object,
    openedQuestions: Array,
  },
  methods: {
    isOpened(category, index) {
      return this.openedQuestions.some(q => q.category === category && q.index === index);
    }
  }
};
</script>

<style scoped>
.game-board {
  width: 100%;
  max-width: 800px;
  margin: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: center;
  padding: 10px;
}

v-btn {
  width: 120px;
  margin: 5px 0;
}

.greyed-out {
  background-color: lightgrey !important;
  pointer-events: none;
}
</style>